import Vue from 'vue'
import './plugins/axios'
import App from './App.vue'
import router from './router'
import './plugins/element.js'
import Qs from 'qs'
Vue.prototype.$Qs = Qs;
Vue.config.productionTip = false

router.beforeEach((to,from,next) => {
  var isLogin = sessionStorage.getItem('isLogin');
  if(isLogin || to.name == "login" || to.name == 'zhuce') {
    next();
  }else {
    next({ path: '/login' })
  }
})
Vue.prototype.$sendData = function(url,params) {
  let promise = new Promise(function(resolve,reject) {
    let data;
    let url2 = process.env.NODE_ENV === "development"? '/api' + url : url;
    Vue.axios.post(url2,params).then((respose) => {
      data = respose.data;
      resolve(data)
    }).catch(error => {
      reject(error.respose)
    })
  })
  return promise
}
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
